@keyframes popup-show {
  0% {
    top: 0px;
    left: 0px;
    transform: translate(-50%, -50%) rotate(1000deg) scale(0.1);
  }
  100% {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(0deg) scale(1);
  }
}

@keyframes btn-show {
  0% {
    bottom: -89px;
    opacity: 0;
    visibility: hidden;
  }
  100% {
    bottom: -49px;
    opacity: 1;
    visibility: visible;
  }
}

@keyframes btn-hide {
  0% {
    opacity: 1;
    visibility: visible;
  }
  100% {
    opacity: 0;
    visibility: hidden;
  }
}

@keyframes bounceInUp {
  from {
    opacity: 0;
    transform: translate3d(-50%, 60px, 0);
    visibility: visible;
  }
  40% {
    opacity: 1;
    transform: translate3d(-50%, -12px, 0);
  }
  65% {
    transform: translate3d(-50%, 7px, 0);
  }
  80% {
    transform: translate3d(-50%, -3px, 0);
  }
  to {
    transform: translate3d(-50%, 0, 0);
    opacity: 1;
    visibility: visible;
  }
}

@keyframes catalog-item-show {
  from {
    transform: scale(0.85) perspective(400px) rotate3d(1, 0, 0, 10deg);
    opacity: 1;
    visibility: visible;
  }
  to {
    transform: scale(1) perspective(400px) rotate3d(1, 0, 0, 0deg);
    opacity: 1;
    visibility: visible;
  }
}

@keyframes catalog-item-hide {
  from {
    transform: scale(1) perspective(400px) rotate3d(1, 0, 0, 0deg);
    opacity: 1;
    visibility: visible;
  }
  33% {
    transform: scale(1) perspective(400px) rotate3d(1, 0, 0, 0deg);
    opacity: 1;
    visibility: visible;
  }
  to {
    transform: scale(0.85) perspective(400px) rotate3d(1, 0, 0, 10deg);
    opacity: 0;
    visibility: hidden;
  }
}

