}

.page-header__contact-number {
  display: block;
  font-size: 22px;
  color: #fff;
  text-decoration: none;
  font-weight: 900;
}

.navigation {
  flex-grow: 1;
}

.navigation__list {
  list-style: none;
  margin: 0;
  padding: 0;
  margin-top: 14px;
  font-size: 0;
}

.navigation__item {
  position: relative;
  display: inline-block;
  vertical-align: top;
  min-height: 32px;
  font-size: 16px;
  line-height: 1.4;
  font-weight: 500;
  border-radius: 16px;
}

.navigation__link--active {
  background-color: #d07058;
  cursor: default;
}

.navigation__link {
  position: relative;
  display: block;
  min-height: 32px;
  padding: 5px 14px;
  line-height: 1.4;
  color: #fff;
  text-decoration: none;
  border-radius: 16px;
  transition: background-color 0.2s, color 0.2s;
}

.navigation__link:not(.navigation__link--active):active, .navigation__link:not(.navigation__link--active):hover {
  background-color: #f7f6f3;
}

.navigation__link:not(.navigation__link--active):active, .navigation__link:not(.navigation__link--active):hover {
  color: #333;
}

.navigation__link:not(.navigation__link--active):active {
  box-shadow: inset 0 2px 1px 0px #696969;
}

.navigation__link:not(.navigation__link--active) .navigation__link-inner {
  display: inline;
  border-bottom: 1px solid rgba(255, 255, 255, 0.4);
}

.navigation__link:hover .navigation__link-inner {
  border-bottom: none;
}

.navigation__link:hover::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -10px;
  height: 20px;
  width: 100%;
}

.navigation__sub-list {
  list-style: none;
  margin: 0;
  padding: 0;
  position: absolute;
  z-index: 2;
  top: 37px;
  left: -7px;
  left: 50%;
  min-width: 143px;
  max-width: 200px;
  width: 110%;
  font-size: 14px;
  font-weight: 400;
  border-radius: 5px;
  background-color: #f8f7f4;
  box-shadow: 0 20px 20px 0 rgba(0, 0, 0, 0.4);
  transform: translateY(-50%) translateX(-50%) scaleY(0);
  transition: transform 0.3s ease-in-out;
}

.navigation__item:hover > .navigation__sub-list {
  transform: translateY(0) translateX(-50%) scaleY(1);
  transition: transform 0.3s ease-in-out;
}

.navigation__sub-title, .navigation__sub-link {
  display: block;
  min-height: 33px;
  padding-left: 20px;
  padding-top: 8px;
  padding-bottom: 8px;
  padding-right: 8px;
  text-decoration: none;
  color: #343434;
}

.navigation__sub-item:last-of-type .navigation__sub-link {
  border-bottom-right-radius: 5px;
  border-bottom-left-radius: 5px;
}

.navigation__sub-title {
  position: relative;
  height: 39px;
  padding-top: 10px;
  font-weight: 500;
}

.navigation__sub-title::after {
  content: "";
  position: absolute;
  left: 6px;
  bottom: 0;
  height: 1px;
  width: 128px;
  background-color: #d1d0ce;
}

.navigation__sub-link:not(.navigation__sub-link--active):hover {
  background-color: #fbded8;
}

.navigation__sub-link:not(.navigation__sub-link--active):active {
  background-color: #f6b5a5;
}

.navigation__sub-link--active {
  background-color: #d07058;
  color: #fff;
}

.functions {
  min-width: 267px;
  max-width: 350px;
  text-align: right;
  font-size: 0;
}

.functions__item {
  display: inline-block;
  vertical-align: top;
  min-height: 32px;
  margin-top: 14px;
  font-size: 14px;
}

.functions__item:not(:last-of-type) {
  margin-right: 10px;
}
