// functions styles
.functions {
  min-width: 267px;
  max-width: 350px;
  text-align: right;
  font-size: 0;


  &__item {
    display: inline-block;
    vertical-align: top;
    min-height: 32px;
    margin-top: 14px;
    font-size: 14px;
  }


  &__item:not(:last-of-type) {
    margin-right: 10px;
  }
}